.Top-store{
margin-top: 80px;
padding: 20px;
margin-right: 10px;
}

.Top-store .ads-wrap{
    display: block;
    gap: 1rem;
    height: 1500px;
    scrollbar-width: none;
    overflow-y: auto;
    width: 250px;
    margin-top: 25px;
}

.Top-store .ads-wrap .ad{
    margin-bottom: 20px;
    padding: 20px;
    width: 250px;
}

.Top-store .ads-wrap .ad .Ad-img{
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
}

.Top-store .ads-wrap .ad img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.Top-store .ads-wrap .ad .ad-desc{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.75rem;
    margin-bottom: 0.85rem;
    font-size: 1.2rem;
}

.Top-store .ads-wrap .ad .ad-desc button{
    padding: 0.5rem;
    font-size: 0.65rem;
    cursor: pointer;
    background-color: #FCBF49;
    border: none;
    color: #fff;
}

.Top-store .ads-wrap .ad p{
    font-size: 0.75rem;
    line-height: 1rem;
}