nav{
    padding: 2px 1%;
    margin: 0;
    justify-content: space-between;
    max-height: 60px;
    align-items: center;
    background: #fff;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 200;

}

.Navbar .Navbar-left .logo{
    width: 60px;
}

.Navbar .Navbar-middle .shop-icon{
    width: 20px;
    margin-right: 40px;
    margin-bottom: 0;
}

