.Navbar-middle .Search-box{
    border: 1px solid #ccc;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 10px 20px;
    border-radius: 25px;
    max-width: 800px;
}

.Navbar-middle .Search-box input{
    width: 500px;
    border: 0;
    outline: 0;
    background: transparent;
}

.Navbar-middle .Search-box img{
    width: 15px;
   
}

.Navbar-right .notify{
    width: 20px;
    margin-right: 25px;
}

.Navbar-right .user-profile{
    width: 35px;
    border: 2px ;
    border-radius: 50%;
    margin-right: 20px;
}

.Navbar-right h4{
    font-size: .8rem;
    margin-bottom: 5px;
}