
.main .middle-container{
    max-width: 800px;
    width: 90%;
    margin-right: 20px;
    margin-left: 40px;
    padding: 10px;
    display: flex;
}

 .main .middle-container .Posts{
    max-width: 600px;
    height: 1500px;
    overflow-y: auto;
    scrollbar-width: none;
    margin-right: 20px;
    margin-top: 5px;
}  

 .main .middle-container .Posts .post{
    background: #fff;
    padding: 1rem;
    border-radius: 1rem;
    width: 100%;
    height: 450px;
    margin: 3rem auto;
    line-height: 1.2;
    font-size: .8rem;
}

 .main .middle-container .Posts .post .post-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main .middle-container .Posts .post .post-top .user{
    display: flex;
    margin-right: 2px;
    gap: 1rem;
}
.main .middle-container .Posts .post .post-top .user .profile-picture{
    width: 35px;
    object-fit: cover;
    height: 35px;
    margin-right: 6px;
    margin-left: 5px;
    position: relative;
    border-radius: 50%;
}

.main .middle-container .Posts .post .post-top .user .info{
    display: flex;
}

.main .middle-container .Posts .post .post-top .user .info .names h3{
    font-size: 12px;
}

.main .middle-container .Posts .post .post-top .user .info .time{
    margin-left: 20rem;
    width: 5px;
}

 .main .middle-container .Posts .post-img{
    /* width: 650px; */
    height: 90%;
} 

.main .middle-container .Posts .post-img img{
    border-radius: 1rem;
    position: relative;
    object-fit: cover;
    overflow: hidden;
    margin: .2rem 0;
    height: 88%;
    width: 100%;
}

.main .middle-container .Posts .post .post-img p{
    margin: .5rem;
    font-size: 0.75rem;
}

.main .middle-container .Posts .post .action-button{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.6rem;
    margin: 0.6rem;
}

.main .middle-container .Posts .post .action-button .interaction-button img{
    margin-right: 10px;
}

.main .middle-container .Posts .post .action-button img{
    width: 15px;
}
