.Trends{
    margin-top: 50px;
    padding: 30px;
    z-index: 200;
    margin-bottom: 2px;
    width: 90%;
    margin-left: 0;
    
}

.Trends .Trend-wrap{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.2rem;
    height: 5rem !important;
    position: initial;
    max-width: auto;
    z-index: 100;
}

.Trends .Trend-wrap .Trend{
    margin-top: 2px;
    margin-left: 20px;
    width: 70px;
    height: 10px;
    margin-bottom: 2px;
    border-left: 0.2px solid;
    padding-bottom: 10px;
}

.Trends .Trend-wrap .Trend .Trend-door{
    width: 55px;
    object-fit: cover;
    padding: 2px;
    height: 55px;
    margin-right: 6px;
    margin-left: 5px;
    position: relative;
    border-radius: 50%;
    border: 2px dashed #fcbf49;
    
}

.Trends .Trend-wrap .Trend  p{
    font-size: 0.65rem;
    text-align: center;
    margin-top: 5px;
}