.Reels{
    display: block;
    gap: .5rem;
    height: 1500px;
    width: 170px;
    overflow-y: auto;
    scrollbar-width: none;
    margin-top: 20px;
    margin-right: 20px;
    z-index: 90;
}



.Reel{
    width: 150px;
    height: 180px;
    position: relative;
    border-radius: 10px;
    margin-bottom: 30px;
    overflow: hidden;
}

.Reel .Profile img{
    position: absolute;
    top: .8rem;
    width: 1.7rem;
    left: 0.2rem;
    border: 2px solid #fcbf49;
    border-radius: 50%;
    
}

.Reel>img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

h1{
    font-size: 11px;
    margin-bottom: 30px;
}